import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import moment from "moment";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, Tooltip } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/generalLedger/period/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import ModalAddAdditionalPeriod from "./modalAddAdditionalPeriod";
import ModalAutoGeneratePeriod from "./modalAutoGeneratePeriod";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class Period extends Component {
  state = {
    filter: {
      period_year: []
    },
    visibleAddAdditionPeriod: false,
    visibleAutoGeneratePeriod: false
  };

  // Get All data diawal
  componentDidMount() {
    const { getPeriods } = this.props;
    getPeriods(null, null, this.props.period.filter);
  }

  componentDidUpdate(prevProps) {
    // Display error notif

    if (this.props.period.period_year !== prevProps.period.period_year) {
      this.setState({
        filter: {
          period_year: this.props.period.period_year
        }
      });
    }
    if (this.props.period.error) {
      this.setState({
        visibleAddAdditionPeriod: false,
        visibleAutoGeneratePeriod: false
      });
      notification("error", this.props.period.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.period.success) {
      this.setState({
        visibleAddAdditionPeriod: false,
        visibleAutoGeneratePeriod: false
      });
      notification("success", this.props.period.success);
      this.props.clearSuccess();
    }
  }

  handleModalAddAdditionalPeriod = () => {
    this.setState({
      visibleAddAdditionPeriod: !this.state.visibleAddAdditionPeriod
    });
  };

  handleModalAutoGeneratePeriod = () => {
    this.setState({
      visibleAutoGeneratePeriod: !this.state.visibleAutoGeneratePeriod
    });
  };

  handleSubmitAddAdditionalPeriod = value => {
    const { storeAdjustingJournalEntry } = this.props;
    storeAdjustingJournalEntry(value);
  };

  handleSubmitAutoGeneratePeriod = value => {
    const { storeAutoGeneratePeriod } = this.props;
    storeAutoGeneratePeriod(value);
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getPeriods } = this.props;
    //const { current_page } = this.props.period;
    const { filter } = this.state;
    let string = "";

    if (filter.period_year) {
      string += "period_year=" + filter.period_year;
    }

    getPeriods(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPeriods } = this.props;
    this.setState({
      filter: {
        period_year: []
      }
    });
    getPeriods();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePeriod(id);
    } else {
      this.props.deactivatePeriod(id);
    }
  };

  listAction = {
    title: "",
    dataIndex: "id_period",
    rowKey: "id_period",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        {row.disabled === false ? (
          <Tooltip
            placement="topLeft"
            title={<IntlMessages id="tooltip.update" />}
          >
            <Link to={`${this.props.match.path}/update/${text}`}>
              <Button
                size="small"
                className="invoiceEdtBtn"
                disabled={row.disabled}
              >
                <i className="ion-android-create" />
              </Button>
            </Link>
          </Tooltip>
        ) : (
          <Button
            size="small"
            className="invoiceEdtBtn"
            disabled={row.disabled}
          >
            <i className="ion-android-create" />
          </Button>
        )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "period_name",
        rowKey: "period_name",
        width: "15%"
      },
      {
        title: getLanguage({ id: "beginDate" }),
        dataIndex: "begin_date",
        rowKey: "begin_data",
        width: "10%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "endDate" }),
        dataIndex: "end_date",
        rowKey: "end_date",
        width: "10%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },

      {
        title: getLanguage({ id: "closedPeriod" }),
        dataIndex: "is_closed",
        rowKey: "is_closed",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = getLanguage({ id: "yes" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "no" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "fixedAssetClosed" }),
        dataIndex: "is_fa_closed",
        rowKey: "is_fa_closed",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = getLanguage({ id: "yes" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "no" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // Data setup
    const { periods, loading } = this.props.period;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Period") && columns.length === 5) {
      columns.push(this.listAction);
    }

    periods.map((period, index) => {
      return dataSource.push({
        ...period,
        key: index
      });
    });

    //console.log(this.props);
    return (
      <>
        <ModalAddAdditionalPeriod
          visible={this.state.visibleAddAdditionPeriod}
          handleModal={this.handleModalAddAdditionalPeriod}
          submit={this.handleSubmitAddAdditionalPeriod}
          loading={loading}
        />
        <ModalAutoGeneratePeriod
          visible={this.state.visibleAutoGeneratePeriod}
          handleModal={this.handleModalAutoGeneratePeriod}
          submit={this.handleSubmitAutoGeneratePeriod}
          loading={loading}
        />

        <LayoutWrapper>
          <Box>
            <PageHeader
              title={<IntlMessages id="periods.pageHeader" />}
              style={{ margin: 0 }}
            >
              <div className="isoInvoiceTableBtn" style={{ marginBottom: "0" }}>
                {/* {permissions.includes("Create Period") ? (
                  <Button
                    type="primary"
                    className="mateAddInvoiceBtn"
                    onClick={this.handleModalAddAdditionalPeriod.bind(this)}
                  >
                    <IntlMessages id="periods.button.add" />
                  </Button>
                ) : null} */}

                {permissions.includes("Create Period") ? (
                  <Button
                    type="primary"
                    className="mateAddInvoiceBtn"
                    style={{ marginLeft: "5px" }}
                    onClick={this.handleModalAutoGeneratePeriod.bind(this)}
                  >
                    <IntlMessages id="periods.button.autoGeneratePeriod" />
                  </Button>
                ) : null}
                <Button
                  onClick={() => this.props.history.replace(redirectTo)}
                  style={{ marginLeft: "5px" }}
                >
                  <IntlMessages id="button.back" />
                </Button>
              </div>
            </PageHeader>

            <div className="isoFilter">
              <Collapse style={customCollapseStyle}>
                <Panel header={<IntlMessages id="filter.header" />} key="1">
                  <div className="isoFilterContent">
                    <div style={{ width: "20%" }}>
                      <label>
                        <IntlMessages id="periodYear" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "period_year"
                        )}
                        value={
                          this.state.filter.period_year
                            ? this.state.filter.period_year
                            : []
                        }
                        style={{ width: "90%" }}
                        placeholder={
                          <IntlMessages id="periods.filter.select.periodYear" />
                        }
                        loading={this.props.period.loading}
                        allowClear={true}
                      >
                        {this.props.period.list_period_year.map((data, key) => (
                          <Option key={key} value={data}>
                            {data}
                          </Option>
                        ))}
                      </Select>
                    </div>
                    <div
                      style={{
                        width: "20%",
                        marginTop: "auto"
                      }}
                    >
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.submit" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </div>
                  </div>
                </Panel>
              </Collapse>
            </div>

            <CardWrapper>
              {periods.length === 0 && !this.props.period.loading ? (
                <Empty
                  description={<IntlMessages id="periods.table.noData" />}
                />
              ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      dataSource={dataSource}
                      columns={columns}
                      loading={this.props.period.loading}
                      className="invoiceListTable"
                      bordered={true}
                      pagination={false}
                    />
                  </Scrollbars>
                </div>
              )}
            </CardWrapper>
          </Box>
        </LayoutWrapper>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    period: state.period,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(Period);
