import React, { Component } from "react";

import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import { Form, DatePicker, Modal } from "antd";
import getLanguage from "../../../App/MyLanguange";

// const { Option } = Select;
// const { TextArea } = Input;

class ModalAutoGeneratePeriod extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.period_year = values.period_year.format("YYYY")
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const { loading } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="periods.button.autoGeneratePeriod" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="25%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label={<IntlMessages id="year" />}
          >
            {getFieldDecorator("period_year", {
              initialValue: moment(),
              rules: [
                {
                  required: true,
                  message: <IntlMessages id="required" />
                },
              ]
              })
              (
                <DatePicker
                  format="YYYY"
                  allowClear={false}
                  style={{ width: "95%" }}
                  mode="year"
                  onPanelChange={value => {
                    setFieldsValue({
                      period_year: value
                    })
                  }}
                />
              )
            }
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAutoGeneratePeriod = Form.create({ name: "modal_auto_generate_period" })(
  ModalAutoGeneratePeriod
);

export default WrappedModalAutoGeneratePeriod;
